#wpadminbar #dpit-tool-bar.dpit-tool-bar {
	position: relative;
	transition: opacity $transition-time-fast ease-in, margin $transition-time-fast ease-in-out;
	height: 100%;
	margin-right: 8px;
	align-items: center;

	@media (max-width: 782px) {
		top: 6px;
		height: 46px;
	}

	&.dpit-tool-bar--inactive {
		margin-right: -100px;
		opacity: 0;
	}

	.dpit-tool-bar__item {
		display: inline-flex;
		align-items: center;
		justify-content: center;
		padding-left: 10px;
		padding-right: 10px;

		@media (max-width: 782px) {
			padding: 1px 10px;
		}


		border: none;
		transition: background $transition-time-slow ease-in-out,
		opacity $transition-time-fast ease-in,
		width $transition-time-med ease-in,
		padding $transition-time-med ease-in;

		&--inactive {
			display: none;
		}

		--bullet-anim-size: 4px;
		--bullet-anim-gap: 4px; // it is half size
		--bullet-anim-color: var(--dpit-uac-prim);
		--bullet-anim-on-color: var(--dpit-uac-prim);
		--bullet-anim-border-radius: 50%;
		--bullet-anim-off-alpha: 0.5;

		&.button-secondary {
			--bullet-anim-color: var(--dpit-uac-prim-bg);
			--bullet-anim-on-color: var(--dpit-uac-prim-bg);
			color: rgb(var(--dpit-uac-sec));

			@include atXS {
				display: none;
			}
		}

		&.dpit-admin-btn {
			position: relative;
			//height: 100%;

			&.dpit-admin-btn--anim {
				min-width: 100px;
			}

			.dpit-anim-rb {
				position: absolute;
				height: 100%;
				width: 100%;

				&__wrap {
					padding: var(--bullet-anim-gap);
					box-sizing: border-box;

					div {
						border-radius: var(--bullet-anim-border-radius);
						padding: var(--bullet-anim-size);
					}
				}
			}
		}

		&--anim {
			background: rgb(var(--dpit-uac-prim));
		}
	}
}